:root {
	--text-secondary: rgba(14, 13, 15, 0.70);
	--white-contrast: #0A090A;
	--purple-main: #6F76C0;
	--mask-gradient: linear-gradient(270deg, rgba(14, 13, 15, 0) 0%, #ECECEC 79.5%);
	--mask-gradient-second: linear-gradient(180deg, rgba(250, 250, 250, 0) 0%, #FAFAFA 100%);
	--accent-gradient: linear-gradient(90deg, #3DAFE2 0%, #6F76C0 100%);
}

.hero-button{
	border-radius: 5px;
	display: inline-flex;
	padding: 20px 32px;
	justify-content: center;
	align-items: center;
	font-style: normal;
	font-weight: 700;
	line-height: 1;
	letter-spacing: 2.1px;
	text-transform: uppercase;
	text-decoration: none;
	color: #fff;
	background: var(--accent-gradient, linear-gradient(90deg, #3DAFE2 0%, #6F76C0 100%));
	transition: all 0.3s ease-in-out;

	&.shadow{
		box-shadow: 0px 16px 24px 0px rgba(61, 175, 226, 0.48);
	}

	&:hover{
		background: linear-gradient(90deg, #3DAFE2 0%, #555BA5 100%);

		&.shadow{
			box-shadow: 0px 8px 12px -6px rgba(61, 175, 226, 0.48);
		}
	}
}

.slide-item-image{
	p{
		font-weight: 700;
		margin-top: 16px !important;
		margin-bottom: 8px;
	}
	.image-box{
		padding: 10px;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #ccc;
		margin-bottom: 16px;
	}
}

.wp-block-blocks-gamestore-block-hero .hero-content .hero-title{
	line-height: 1.2;
}
.wp-block-blocks-gamestore-block-hero .hero-content .hero-title,
.wp-block-blocks-gamestore-block-hero .hero-content p{
	margin-top: 0;
	margin-bottom: 0;
}

.is-root-container.is-desktop-preview{
	overflow: hidden;
}
